img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 60px;
}

#slider {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
.slider-container {
  min-height: 480px;
}
.slider-container picture {
  transition: all 0.5s ease;
  opacity: 0;
  width: 100%;
  position: absolute;
  z-index: 1;
}
.slider-container picture.active {
  opacity: 1;
}

.hero-picture-block {
  display: block;
  position: relative;
  width: 100%;
  height: auto;
}
.hero-picture {
  display: block;
}

.organic-food-icon {
  position: absolute;
  top: -7px;
  right: 8px;
  z-index: 99;
}

.hero {
  background-color: var(--primary-bg-color);
  padding-top: 37px;
  padding-bottom: 37px;
}

.hero-title {
  color: var(--title-black-text-color);
  text-align: center;
  font-family: 'Neue Montreal Medium';
  font-size: 44px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.09;
  letter-spacing: -0.02em;
  width: 100%;
  margin: 0 auto 16px;
}

.hero-title-accent {
  color: var(--secondary-bg-green-color);
}

.hero-text {
  width: 100%;
  color: var(--hero-color);
  text-align: center;
  font-family: 'Neue Montreal Medium';
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.09;
  letter-spacing: -0.01em;
  margin: 0 auto;
  margin-bottom: 31px;
}
