  --primary-bg-green-color: #586f1f;
  --secondary-bg-green-color: #6d8434;
  --card-bg-color: #fafafa;
  --cancel-background: #01010112;
  --backdrop-background: #01010166;
  --modal-text: #010101b3;
  --pagination-color: #a3a3a3;
  --white-color: #fff;
  --span-info-color: #0101014d;
  --hero-color: #010101cc;
  --footer-color: #e8e8e299;
