body {
  background: var(--primary-bg-color);
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

main {
  flex: 1;
}

footer {
  flex-shrink: 0;
}

.cart-products {
  max-height: 540px;
  margin-bottom: 80px;
  padding-right: 40px;
  overflow-x: hidden;
}

.section-cart {
  padding-top: 37px;
  padding-bottom: 80px;
}

.cart-title {
  color: var(--title-black-text-color);
  text-transform: uppercase;
  align-items: center;
  margin-bottom: 14px;
