  align-items: flex-end;
  width: 100%;
  margin-bottom: 30px;
  padding: 14px 18px;
  display: flex;
}

.cart-total-price-text {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-total-price-number {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.cart-total-price-sum {
