}

.cart-form-input {
  border: 1px solid var(--span-info-color);
  color: var(--title-black-text-color);
  border-radius: 30px;
  outline: #0000;
  margin-bottom: 14px;
  padding: 12px 12px 12px 45px;
  font-family: Neue Montreal Regular;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.cart-form-btn {
  background: var(--secondary-bg-green-color);
  color: var(--title-white-text-color);
  border: none;
  border-radius: 30px;
  outline: #0000;
  width: 100%;
  padding: 12px;
  font-family: Neue Montreal Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.cart-empty {
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 105px 0 120px;
  display: flex;
}

.cart-empty-retina {
  flex-direction: column;
  align-items: center;
