}

.cart-empty-img {
  width: 132px;
  height: 114px;
  margin-bottom: 28px;
}

.cart-empty-text {
  color: var(--title-black-text-color);
  margin-bottom: 14px;
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.11;
}

.cart-empty-description {
  width: 90%;
}

.cart-empty-link {
  color: var(--secondary-bg-green-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.11;
  text-decoration: none;
}

.cart-empty-description {
  color: var(--secondary-text-color);
  text-align: center;
  font-family: Neue Montreal Regular;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
}

.filters {
  background-color: var(--primary-bg-color);
  padding-top: 37px;
  padding-bottom: 20px;
}

.filters-title {
  color: var(--span-info-color);
  margin-bottom: 10px;
  font-size: 14px;
}

.filters-form {
  flex-direction: column;
  gap: 14px;
  width: 100%;
  display: flex;
}

.filters-input::placeholder {
  color: #010101;
  font-family: Neue Montreal Regular;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.filters-input, .filters-categories, .filters-allTypes {
  cursor: pointer;
}

.filters-search-wrap, .filters-wrap {
  position: relative;
}

.filters-input, .filters-categories, .filters-allTypes {
  border: 1px solid var(--span-info-color);
  background-color: #0000;
  border-radius: 30px;
  padding: 14px 20px;
  font-size: 14px;
}

.filters-allTypes {
  background-color: var(--card-bg-color);
  border-color: #0000;
}

.filters-input:focus {
  outline: 1px solid var(--secondary-bg-green-color);
}

.filters-search-btn {
  background-color: #0000;
  border: none;
  place-content: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: -2px;
  transform: translate(-50%, -50%);
}

.filters-down-svg {
  fill: var(--primary-bg-color);
  stroke: var(--title-black-text-color);
  transition: all .3s;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
}

.rotate {
  transform: translate(-50%, -50%)rotate(180deg);
}

.filters-categories-list, .filters-allTypes-list {
  z-index: 2;
  background: var(--white-color);
  visibility: hidden;
  opacity: 0;
  border-radius: 15px;
  width: 100%;
  padding: 14px 20px;
  transition: all .3s;
  position: absolute;
  top: 56px;
  left: 0;
  box-shadow: 0 4px 11.2125px #0000000a, 0 20px 69px #00000012;
}

.filters-allTypes-list {
  z-index: 1;
}

.list-active {
  visibility: visible;
  opacity: 1;
  border: 1px solid var(--span-info-color);
}

.filters-categories-item, .filters-allTypes-item {
  color: var(--filter-color);
  cursor: pointer;
  margin-bottom: 9px;
  font-size: 14px;
}

.filters-categories-item:hover, .filters-allTypes-item:hover {
  color: var(--title-black-text-color);
}

.footer-section {
